import WhiteNavBar from "./WhiteNavBar";
import Modal from "../secondaryelements/Modal";
import React,{Component} from 'react';
import {Link, navigate} from '@reach/router';
import Signature from "./Signature";
import LoadingBar from "../secondaryelements/LoadingBar";
import PageSwitcher from "../secondaryelements/PageSwitcher";
import Elements from "./Elements";
import SearchBar from "../secondaryelements/SearchBar";
import YesOrNo from "../secondaryelements/YesOrNo";
import Embellishment from "../secondaryelements/Embellishment";
import Carriers from "../secondaryelements/Carriers";
import GeneralField from "../secondaryelements/GeneralField";

function Body(props){
  const elements = new Elements();

  let myYears=[]
  for (let index = 0; index < 24; index++) {
    myYears.push(2021-index);
  }

  async function selectYear(e){
    let myState={...props.userPack.data}
    myState.selectedYear=e.target.getAttribute("value")
    window.localStorage.setItem('selectedYear',e.target.getAttribute("value"))
    props.userPack.methods.stateHandling("data",myState)
    navigate(e.target.parentElement.getAttribute('value'))
  }

  const infoWriter=(e)=>{
    let myState={...props.userPack.data}
    e.preventDefault();
    e.stopPropagation();
    handleData(e.target.getAttribute("name"),e.target.value)
    console.log("bobo",e.target.value,e.target.getAttribute("name"))
  //   myState[e.target.getAttribute("name")]=e.target.getAttribute("value")
  //   window.localStorage.setItem(e.target.getAttribute("name"),e.target.getAttribute("value"))
  //   props.userPack.methods.stateHandling("data",myState)

  }
  const handleData=(name,value)=>{

    let myState={...props.userPack.data}
    myState[name]=value
    props.userPack.methods.stateHandling("data",myState)
    window.localStorage.setItem(name,value)
  }
  async function selector(e){
    let myState={...props.userPack.data}
    const nameCode =e.target.getAttribute("name")
    const value =e.target.getAttribute("value")
    e.preventDefault()
    myState[nameCode]=value
    if(nameCode=="selectedBrand"){
      let myModels=await props.userPack.methods.axios.post(props.userPack.methods.baseUrl + `getModels/`,{"brand":value}).then(resp=>{
            return resp.data
          })
      myState.models=myModels
    }
    props.userPack.methods.stateHandling("data",myState)
    window.localStorage.setItem(nameCode,value)
    let myEl = document.getElementById('myBox')
    if(myEl){
      myEl.classList.add("outEffect")
      await new Promise(r => setTimeout(r, 10));
    }
    navigate(e.target.parentElement.getAttribute('value'))
  }

  let myTitle1
  let myTitle2
  switch (props.userPack.data.step) {
    case 0:
      myTitle1 = "YEAR"
      break;
      case 1:
        myTitle1 = "MAKE"
        break;
        case 2:
          myTitle1 = "MODEL"
          break;
          case 3:
            myTitle1 = "TYPE"
            break;
    default:
      myTitle1 = "Not-defined"
      break;
  }
  let filteredBrands=[]
  let filteredModels=[]
  
  elements.brands.forEach(element => {
    let myElement = element.toLowerCase()
    if(myElement.includes(props.userPack.filterSearch.toLowerCase())){
      filteredBrands.push(element)
    }
  });
  if(filteredBrands===[]){
    filteredBrands= elements.brand
  }
  if(window.location.pathname.includes('model')){
  props.userPack.data.models.forEach(element => {
    let myElement = element.toLowerCase()
    if(myElement.includes(props.userPack.filterModelSearch.toLowerCase())){
      filteredModels.push(element.model)
    }
  })}
  
  const secondVehicle=(e,bool)=>{
    e.preventDefault()
    e.stopPropagation()
    let newState={...props.userPack.data}
    newState.ogVehicle={brand:newState.selectedBrand,model:newState.selectedModel,year:newState.selectedYear}
    newState.selectedBrand=''
    newState.selectedModel=''
    newState.selectedYear=''
    newState.vehicleAmount=newState.vehicleAmount+1
    window.localStorage.setItem('selectedSecondBrand',newState.ogVehicle.brand)
    window.localStorage.setItem('selectedSecondModel',newState.ogVehicle.model)
    window.localStorage.setItem('selectedSecondYear',newState.ogVehicle.year)
    window.localStorage.setItem('selectedBrand','')
    window.localStorage.setItem('selectedModel','')
    window.localStorage.setItem('selectedYear','')
    
    props.userPack.methods.stateHandling('data',newState)
    if(!bool){
      navigate(`/newquote/${props.userPack.pathAux[props.userPack.data.step+2]}`)
    }else{
      navigate(`/newquote/select-year`)}
   
  }
  const secondDriver=(e,bool)=>{
    e.preventDefault()
    e.stopPropagation()
    console.log("y qloq")
    let newState={...props.userPack.data}
    newState.ogDriver={firstName:newState.firstName,lastName:newState.lastName,dob:newState.dateOfBirth,male:newState.male}
    newState.firstName=''
    newState.lastName=''
    newState.dateOfBirth=''
    newState.male=''
    newState.driverAmount=newState.driverAmount+1
    window.localStorage.setItem('secondFirstName',newState.ogDriver.firstName)
    window.localStorage.setItem('secondLastName',newState.ogDriver.lastName)
    window.localStorage.setItem('secondDob',newState.ogDriver.dateOfBirth)
    window.localStorage.setItem('secondMale',newState.ogDriver.male)
    window.localStorage.setItem('firstName','')
    window.localStorage.setItem('lastName','')
    window.localStorage.setItem('dob','')
    window.localStorage.setItem('male','')
    
    props.userPack.methods.stateHandling('data',newState)
    if(!bool){
      navigate(`/newquote/${props.userPack.pathAux[props.userPack.data.step+1]}`)
    }else{
      navigate(`/newquote/select-name`)}
    
  }
  const saveName=(e)=>{
    e.preventDefault()
    e.stopPropagation()
    navigate(`/newquote/${props.userPack.pathAux[props.userPack.data.step+1]}`)
  }
  const next=(e)=>{
    e.preventDefault()
    e.stopPropagation()
    navigate(`/newquote/${props.userPack.pathAux[props.userPack.data.step+1]}`)
  }
  const skip=(e)=>{
    e.preventDefault()
    e.stopPropagation()
    if(props.userPack.data.driverAmount>1){
      navigate(`/newquote/${props.userPack.pathAux[props.userPack.data.step+2]}`)
    }else{
    navigate(`/newquote/${props.userPack.pathAux[props.userPack.data.step+1]}`)}
  }
  const results=(e)=>{
    e.preventDefault()
    e.stopPropagation()
    navigate(`/newquote/results`)
  }
  const CurrentStep =(props)=>{switch (props.userPack.data.step) {
        case 0:
        return(
        <>

        {myYears.map((year,index)=>(
        <div key={'outterYear'+index} value='/newquote/select-model'>
        <div key={'innerYear'+index} className={props.userPack.data.selectedYear==year?"blueBox center blueSelected":"blueBox center"} onClick={selectYear} value={year}>{year}</div>
        </div>
        ))}
        </>)
        case 1:
        return(
        <>
        <div className="myRow center" style={{margin:"10px"}}>
        <SearchBar userPack={props.userPack} name="filterSearch" value={props.userPack.filterSearch}/></div>
        {filteredBrands.slice(0,18).map((brand,index)=>(
        <div key={'outterBrand'+index} value='/newquote/select-brand'>
        <div key={'innerBrand'+index} style={{minWidth:"148px"}} name="selectedBrand" 
        className={props.userPack.data.selectedBrand==brand?"blueBox center blueSelected":"blueBox center"} onClick={selector} value={brand}>{brand}</div>
        </div>
        ))}
        </>)
        case 2:
        return(
        <>
        <div className="myRow center" style={{margin:"10px"}}>
        <SearchBar name="filterModelSearch" value={props.userPack.filterModelSearch}
        userPack={props.userPack}/></div>
        {filteredModels.slice(0,18).map((model,index)=>(
        <div key={'outterAdditionalCar'+index} value={props.userPack.data.vehicleAmount>1?'/newquote/select-insured':'/newquote/select-additionalCar'}>
        <div key={'innerAdditionalCar'+index} style={{minWidth:"148px"}} name="selectedModel" 
        className={props.userPack.data.selectedModel==model?"blueBox center blueSelected":"blueBox center"} onClick={selector} value={model}>{model}</div>
        </div>
        ))}
        </>)
        case 3:
        return(
        <>
        <YesOrNo navigationUrl='/newquote/select-year' action={secondVehicle} name="additionalVehicle" title="Add Second Vehicle? (Save up to 25%)" userPack={props.userPack}/>
        </>)
        case 4:
        return(
        <>
        <YesOrNo name="insured" title="Are You Currently Insured?" userPack={props.userPack}/>
        </>)
        case 5:
        return(
        <>
        <YesOrNo name="homeOwner" title="Are You a Home Owner?" userPack={props.userPack}/>
        </>)
        case 6:
        return(
        <>
        <YesOrNo name="married" title="Are You Married?" userPack={props.userPack}/>
        </>)
        case 7:
        return(
        <>
        <YesOrNo name="accident" title="Any at-fault Accidents in the Last 3 years?" userPack={props.userPack}/>
        </>)    
        case 8:
        return(
        <>
        <YesOrNo name="dui" title="DUI in the Last 3 years?" userPack={props.userPack}/>
        </>)  
        case 9:
        return(
        <>
        <GeneralField name="dui" title={props.userPack.data.driverAmount<2?"What is Your Name":"What is His/Her Name"} userPack={props.userPack}>
        <form onSubmit={saveName} style={{width:"100%"}}>
        <div className="flexWrap">
          
            <div className="myRow">
            <input required={true}  type="text" name="firstName" id="firstName"  value={props.userPack.data.firstName}
            onChange={infoWriter}
            className="generalField" placeholder="First Name" style={{width:"100%"}} />
            </div>
            <div className="myRow"  style={{marginTop:"14px"}}>
            <input required={true} type="text" name="lastName" id="lastName" className="generalField" 
             value={props.userPack.data.lastName}
             onChange={infoWriter}placeholder="Last Name" style={{width:"100%"}} />
            </div>
            <div className="myRow center"  style={{marginTop:"32px"}}>

            <input style={{minWidth:"200px"}} className="blueBox center" type="submit" value="Continue"/>

            </div>
         
        </div>
        </form>
       </GeneralField>
        </>)  
                case 10:
                  return(
                  <>

                  <GeneralField name="dui" title={props.userPack.data.driverAmount<2?"What is Your Date of Birth?":"What is His/Her Date of Birth?"} userPack={props.userPack}>
                  <form onSubmit={saveName} style={{width:"100%"}}>
                  <div className="flexWrap">
                    <div className="myRow">
                    <input required={true} autoFocus={true}
                    onChange={infoWriter} value={props.userPack.data.dateOfBirth} 
                    type="date" name="dateOfBirth" id="dateOfBirth" className="generalField"
                     placeholder="xx/xx/xxxx" style={{width:"100%"}} />
                    </div>
                    <div className="myRow center"  style={{marginTop:"32px"}}>
                    <input style={{minWidth:"200px"}} className="blueBox center" type="submit" value="Continue"/>
                    </div>
                    </div>
                    </form>
                 </GeneralField>
                  </>) 
                          case 11:
                            return(
                            <>
                             {props.userPack.data.driverAmount<2?
                            <YesOrNo action={next} option1="Female" option2="Male" name="male" title="Are you Male or Female?" userPack={props.userPack}/>
                            :
                            <YesOrNo action={skip} option1="Female" option2="Male" name="male" title="Is He/She Male or Female?" userPack={props.userPack}/>
                            } </>)  
        case 12:
          return(
          <>
          <YesOrNo navigationUrl="/newquote/select-name" action={secondDriver} name="additionalDriver" title="Add Second Driver? (Save up to 25%)" userPack={props.userPack}/>
          </>)  
        case 13:
        return(
        <>
        <YesOrNo name="military"
        title="Have You or Anyone in Your Family Served in the Military?" userPack={props.userPack}/>
        </>)  
        case 14:
        return(
        <>
                <GeneralField name="dui" title="What is Your Address?" userPack={props.userPack}>
                  <form onSubmit={next}>
        <div className="flexWrap">
          <input required={true} value={props.userPack.data.address} onChange={infoWriter} 
           type="text" name="address" id="address" className="generalField" placeholder="Address" style={{marginBottom:"1rem",width:"72%"}} />
          <input type="text" value={props.userPack.data.unit} onChange={infoWriter} name="unit" id="" className="generalField" placeholder="Unit #" style={{marginBottom:"1rem",marginLeft:"1rem",width:"25%"}} />
          <input required={true} value={props.userPack.data.city} onChange={infoWriter} type="text" name="city" id="" className="generalField" placeholder="City" style={{marginBottom:"1rem",width:"100%"}} />
          <input required={true} value={props.userPack.data.state} onChange={infoWriter} type="text" name="state" id="" className="generalField" placeholder="State" style={{width:"48.5%"}} />
          <input required={true} value={props.userPack.data.zipcode} onChange={infoWriter} type="text" name="zipcode" id="" className="generalField" placeholder="Zipcode" style={{marginLeft:"1rem",width:"48.5%"}} />
          <div className="myRow center"  style={{marginTop:"32px"}}>
         <input type="submit" value="Continue" className="blueBox center" style={{minWidth:"200px"}}/>
          </div>
          </div>
          </form>
       </GeneralField>
        </>)  
        case 15:
        return(
        <>
        <GeneralField name="dui" title="What is Your Email?" userPack={props.userPack}>
        <form onSubmit={next}>
        <div className="flexWrap">
          
          <div className="myRow"> 
          <input required={true} autoFocus={true} value={props.userPack.data.email} 
          onChange={infoWriter} type="email" name="email" id="email" className="generalField" placeholder="Email" style={{width:"100%"}} />
          </div>
          <div className="myRow center"  style={{marginTop:"32px"}}>
          <input type="submit" value="Continue" className="blueBox center" style={{minWidth:"200px"}}/>
          </div>
          </div>
          </form>
       </GeneralField>
        </>)  
        case 16:
        return(
        <>
        <GeneralField name="dui" title="What is Your Phone Number?" userPack={props.userPack}>
          <form onSubmit={results}>
        <div className="flexWrap">
          <div className="myRow">
          <input required={true} autoFocus={true} value={props.userPack.data.phone}
          onChange={infoWriter}  type="tel" name="phone" id="phone" className="generalField" placeholder="(xxx) xxx-xxxx" style={{width:"100%"}} />
          </div>
          <div className="myRow center"  style={{marginTop:"32px"}}>
          <input type="submit" value="Continue" className="blueBox center" style={{minWidth:"200px"}}/>
          </div>
          </div>
          </form>
       </GeneralField>
        </>)  
    default:
      return(<>
      </>)
  }}



  return(
      <>
     {props.userPack.data.loading
     ?
     <>
      <div className="myRow center" style={{marginTop:"220px",height:"100px"}}>
       <div class="spinner">
    <div class="spinner-item"></div>
    <div class="spinner-item"></div>
    <div class="spinner-item"></div>
    <div class="spinner-item"></div>
    <div class="spinner-item"></div>
  </div>
  </div>
     </>
     :
     <>
      <div className="myRow center">
          <span className="" style={{marginTop:"100px",fontSize:"20px",
        width:"90vw !important",backgrouncColor:"black",color:"#000000"}}>
          Compare Insurance Rates in Florida</span>
      </div>

      <div className="myRow center" style={{marginTop:"12px"}}>
          <LoadingBar userPack={props.userPack} width={props.userPack.dimensions.width} percentage={0.84}/>
      </div>
      {props.userPack.data.step<3?
      <>
       <div className="myRow center">
      <div className="myRow center" style={{width:"82vw",marginTop:"12px"}}>
      <span className="textAlign" style={{marginTop:"10px",fontSize:"28px",
        width:"60vw !important",backgrouncColor:"black",color:"#000000"}}>
          Let’s Get Started!
          </span>
          </div>
          </div>
      </>
      :<></>}
     

      <div className="myRow center" style={{marginTop:"26px"}}>
          <div className="flexWrap center" id="myBox" style={{width:"540px"}}>
            
            {/* <CurrentStep localPack={props.localPack} userPack={props.userPack}/> */}
            <div className="myRow center" style={{width:"82vw",marginTop:"-5px"}}>
              <span className="karlaBold textAlign" style={{width:"82vw",marginTop:"",fontSize:"28px"}}>{props.stepLine.label}

            </span>
           
          </div>
          <div className="flexWrap center" style={{marginTop:"20px"}}>
          {props.children}
          </div>
         
      </div>
      </div>
    </>
    }
    </>
  )
  
}

class NewQuote extends Component {
  state = { filterSearch:"" }



  stateHandling=(tag,latter)=>{
    this.setState({[tag]:latter})
  }
  componentWillUnmount(){
    //
  }
  componentDidMount(){
    const that=this
    async function fetcher(){
      let myVar =window.location.pathname.substr(11,window.location.pathname.length)
      let result=[]
      let newData = {...that.props.userPack.data}
      //let myBrand=window.localStorage.getItem('selectedBrand')
      let myBrand=that.props.userPack.data.brand
      let myEl = document.getElementById('myBox')
      console.log("branding",myBrand,"eo")
      
      if(that.props.userPack.data.brand){
        if(myBrand!=null){
          result= await that.props.userPack.methods.axios.post(that.props.userPack.methods.baseUrl + `getModels/`,{"brand":myBrand}).then(resp=>{
            return resp.data
          })
          if(myVar=="select-model"&&result.length===0){
            navigate(that.props.userPack.data.steps[1])
            return
          }
        }
        }

      if(myEl){
        await new Promise(r => setTimeout(r, 290));
        
        myEl.classList.remove("inEffect")
        
      }
        newData.steps=that.props.steps
        const elements = new Elements();
        let filteredBrands=[]
        let filteredModels=[]
 

        result.forEach(function(element,index){
          filteredModels.push(element.model)
        })
        
        elements.brands.forEach(element => {
          let myElement = element.toLowerCase()
          if(myElement.includes(that.props.userPack.filterSearch.toLowerCase())){
            filteredBrands.push(element)
          }
        });
        if(filteredBrands===[]){
          filteredBrands= elements.brand
        }
        if(window.location.pathname.includes('model')){
          that.props.userPack.data.models.forEach(element => {
          let myElement = element.toLowerCase()
          if(myElement.includes(that.props.userPack.filterModelSearch.toLowerCase())){
            filteredModels.push(element)
          }
        })}
        for (let index = 0; index < that.props.steps.length; index++) {
          const element = that.props.steps[index];
          if(element==myVar){
            newData.step=index
            break
          }
          
        }

        that.props.names.forEach(element => {
          if(window.localStorage.getItem(element)){
            newData[element]=window.localStorage.getItem(element)
          }
        })
        newData.firstName=window.localStorage.getItem("firstName")
        newData.lastName=window.localStorage.getItem("lastName")
        newData.city=window.localStorage.getItem("city")
        newData.state=window.localStorage.getItem("state")
        newData.zipcode=window.localStorage.getItem("zipcode")
        newData.unit=window.localStorage.getItem("unit")
        newData.models=filteredModels
        newData.filteredBrands=filteredBrands
        newData.filteredModels=filteredModels
        newData.loading=false
        that.props.userPack.methods.stateHandling('data',newData)
        that.setState({step:newData.step})
      }
    
    fetcher()
  }
  render() { 
    return ( <>

      <WhiteNavBar userPack={this.props.userPack}/>
      <Body userPack={this.props.userPack} steps={this.props.steps} stepLine={this.props.stepLine}>
        {this.props.stepLine.component}
      </Body>
      <PageSwitcher userPack={this.props.userPack}/>
      <Embellishment/>
      <Carriers/>
      <Signature userPack={this.props.userPack}/>
    </> );
  }
}
 
export default NewQuote;