import WhiteNavBar from "./WhiteNavBar";
import Modal from "../secondaryelements/Modal";
import React from 'react';
import InsuranceLanding from "./InsuranceLanding";
import Signature from "./Signature";

function MainView(props){
  const switcher=(e)=>{
    let neoModal={...props.userPack.modal}
    neoModal.active=!neoModal.active
    props.userPack.methods.stateHandling('modal',neoModal)
  }
    return(
      <>
      <Modal switcher={switcher} active={props.userPack.modal.active}>

      </Modal>
        <WhiteNavBar userPack={props.userPack}/>
        <InsuranceLanding switcher={switcher} userPack={props.userPack}/>
        <Signature userPack={props.userPack}/>
      {props.children}
      </>
    )
  }

  export default MainView;